.grid .w-1-1 {
  width: 100%;
}
.grid .half,
.grid .w-1-2 {
  width: 50%;
}
.grid .one-third,
.grid .w-1-3 {
  width: 33.3332%;
}
.grid .two-thirds,
.grid .w-2-3 {
  width: 66.6665%;
}
.grid .one-quarter,
.grid .one-fourth,
.grid .w-1-4 {
  width: 25%;
}
.grid .three-quarters,
.grid .three-fourths,
.grid .w-3-4 {
  width: 75%;
}
.grid .one-fifth,
.grid .w-1-5 {
  width: 20%;
}
.grid .two-fifths,
.grid .w-2-5 {
  width: 40%;
}
.grid .three-fifths,
.grid .w-3-5 {
  width: 60%;
}
.grid .four-fifths,
.grid .w-4-5 {
  width: 80%;
}
.grid .golden-small,
.grid .w-g-s {
  width: 38.2716%;
